type IWeightUnit = '' | '';
type IVolumeUnit = '' | '';
type IPieceUnit = '';
type IMeasurableUnit = IWeightUnit | IVolumeUnit;

export type ISize = {
  min: number;
  max: number;
  unit: IMeasurableUnit;
  isApproximate: boolean;
};

type IPrice<T> = {
  value: number;
  unit: T;
  display: string;
};

type IBaseProduct<T> = {
  name: string;
  price: IPrice<T>; //   ,  "600/"
  image: string; //   
  orderBy: [number, T]; //         
  exportIn: T | IPieceUnit;
  consistsOf: string; //  
  approximateSize?: ISize; //   

  unavailable?: boolean; //       
  promo?: {
    name: string; //   -  "15% ",  "  100"
    price: string; //  ,  "510/"
  };
};

export type IProduct = IBaseProduct<IWeightUnit> | IBaseProduct<IVolumeUnit> | IBaseProduct<IPieceUnit>;

